import axios from 'axios';
// Dans api.js ou votre service d'appel
console.log("Recherche pour:", nom, prenom);
console.log("Résultat trouvé:", membre);


const API_URL = 'http://localhost:4000';

export const checkMembership = async (nom, prenom) => {
  try {
    const response = await axios.get(`${API_URL}/members`, {
      params: { nom, prenom }
    });
    
    return response.data;
  } catch (error) {
    if (error.response) {
      // Le serveur a répondu avec un code d'erreur
      if (error.response.status === 404) {
        return { 
          success: false, 
          isPaid: false, 
          message: "Aucun membre trouvé avec ce nom et prénom." 
        };
      }
      throw new Error(error.response.data?.message || "Erreur du serveur");
    } else if (error.request) {
      // La requête a été envoyée mais pas de réponse
      throw new Error("Pas de réponse du serveur - Vérifiez votre connexion");
    } else {
      // Erreur lors de la configuration de la requête
      throw new Error("Erreur de configuration de la requête");
    }
  }
};
